  position: absolute;
  right: 0;
  bottom: -5px;
}
.custom-select .input-message.is-invalid {
  color: #ff0000;
}
.custom-select .input-message.is-valid {
  color: #15ab07;
}

.header {
  padding-top: 30px;
}
.header .container {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.header__burger {
  display: none;
}
@media (max-width: 1023px) {
  .header {
    padding-top: 20px;
  }
  .header .container {
    flex-direction: column;
  }
  .header .header__logo {
    margin-bottom: 30px;
  }
  .header .header__controls-wrapper {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
  }
  .header__burger {
    margin: 0;
    padding: 0;
    background-color: transparent;
    border: 0;
    outline: 0;
    cursor: pointer;
    z-index: 10;
  }
  .header__burger, .header__burger::before, .header__burger::after {
    display: block;
    background-color: #4a4a4a;
    height: 4px;
    width: 30px;
    transition: transform 400ms cubic-bezier(0.23, 1, 0.32, 1);
    border-radius: 2px;
  }
  .header__burger::before {
    content: "";
    margin-top: -8px;
  }
  .header__burger::after {
    content: "";
    margin-top: 12px;
  }
  .header--open .header__burger {
    background: rgba(255, 255, 255, 0);
  }
  .header--open .header__burger::before {
    margin-top: 0;
    transform: rotate(405deg);
  }
  .header--open .header__burger::after {
    margin-top: -4px;
    transform: rotate(-405deg);
  }
  .header:not(.header--open) .main-nav {
    transform: translateX(-100%);
    opacity: 0;
    visibility: hidden;
    transition-delay: 0s, 0.6s, 0.6s;
  }
}

.main-nav__list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}
.main-nav__item {
  position: relative;
  margin-right: 45px;
}
.main-nav__item:last-of-type {
  margin-right: 0;
}
.main-nav__item a {
  position: relative;
  color: #4a4a4a;
  transition: color 0.3s ease;
}
.main-nav__item a:hover {
  color: #000000;
}
.main-nav__item .active::after {
  content: "";
  position: absolute;
  top: 25px;
  left: 50%;
  transform: translateX(-50%);
  height: 2px;
  width: 21px;
  background-color: #fb9b69;
}
@media (max-width: 1023px) {
  .main-nav {
    position: fixed;
    top: 65px;
    left: 0;
    right: 0;
    width: 100%;
    background-color: #48b774;
    opacity: 98%;
    transition: transform 0.3s ease, opacity 0s, visibility 0s;
    z-index: 10;
  }
  .main-nav .main-nav__list {
    flex-direction: column;
    padding-top: 25px;
  }
  .main-nav .main-nav__item {
    width: 100%;
    margin-right: 0;
  }
  .main-nav .main-nav__item a {
    color: #ffffff;
    display: block;
    padding: 16px;
  }
  .main-nav .active {
    background-color: #fb9b69;
  }
  .main-nav .active::after {
    display: none;
  }
}

